<template>
  <div class="flex-1 baidu-maps"
      v-loading="loading"
      element-loading-text="加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
    <div class="scroll result">
      <div class="result-item" :key="item.title" v-for="item in geocoderResult">
        <div class="result__title">{{item.title}}</div>
        <div class="result__address">{{item.address}}</div>
      </div>
    </div>
    <BaiduMap
      class="bm-view"
      :ak="bmConfig.ak"
      :center="bmConfig.center"
      :zoom="bmConfig.zoom"
      :dragging="bmConfig.dragging"
      :map-click="bmConfig.mapClick"
      :scroll-wheel-zoom="bmConfig.scrollWheelZoom"
      :double-click-zoom="bmConfig.doubleClickZoom"
      :keyboard="bmConfig.keyboard"
      :pinch-to-zoom="bmConfig.pinchToZoom"
      @ready="handler"
    >
      <BmGeolocation anchor="BMAP_ANCHOR_TOP_RIGHT" :showAddressBar="true" :autoLocation="true" />
      <bm-circle :center="bmConfig.center" :radius="500" :stroke-weight="2"></bm-circle>
      <bm-marker :position="bmConfig.center"></bm-marker>
    </BaiduMap>
  </div>
</template>

<script>
import { BaiduMap, BmGeolocation, BmCircle, BmMarker } from 'vue-baidu-map'
export default {
  name: 'BaiduMaps',
  components: {
    BaiduMap,
    BmGeolocation,
    BmCircle,
    BmMarker
  },
  data () {
    return {
      loading: false,
      geocoderResult: {},
      bmConfig: {
        ak: '6IgUio3pgB7dHoQkgllpi9voF4nkxuMH', // 密钥
        center: {
          lng: 116.403963,
          lat: 39.915119
        },
        zoom: 15, // 缩放等级
        dragging: false, // 允许拖拽
        mapClick: false, // 允许点击
        scrollWheelZoom: false, // 允许鼠标滚轮缩放
        doubleClickZoom: false, // 允许双击缩放
        keyboard: false, // 允许键盘操作
        pinchToZoom: false // 允许双指缩放
      }
    }
  },
  methods: {
    handler ({ BMap, map }) {
      this.loading = true
      const geolocation = new BMap.Geolocation()
      geolocation.getCurrentPosition(GeolocationResult => {
        const { point } = GeolocationResult
        // 创建地理编码（地理解析器）实例
        const myGeo = new BMap.Geocoder()
        // 根据坐标得到地址描述
        myGeo.getLocation(point, result => {
          if (result) {
            const { point } = result
            this.bmConfig.center = point
            this.geocoderResult = result
            if (result.surroundingPois.length > 0) {
              this.geocoderResult = result.surroundingPois
            }
          } else {
            alert('error')
          }
          this.loading = false
        }, {
          poiRadius: 1000,
          numPois: 10
        })
      }, {
        enableHighAccuracy: true,
        maximumAge: 1000
      })
    }
  }
}
</script>

<style scoped lang="less">
.bm-view {
  height: 100%;
  ::v-deep .anchorBL {
    display: none;
  }
}

.result {
  max-height: 30vh;
  padding: 10px;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  background: rgba(255,255,255,.9);
  overflow: auto;
}
.result-item + .result-item {
  margin-top: 10px;
}
.result__title {
  font-weight: bold;
}
.result__address {}
</style>
